import treeHelper from '../base/TreeHelper';
import SideMenuItems from './SideMenuItems';
import SideMenuIcon from './SideMenuIcon';

export const SIDEMENU_TEMPLATE = `
<div class="f-column">
    <div v-if="collapsed" class="sidemenu sidemenu-collapsed f-full">
        <div :class="['accordion',{'accordion-noborder':!border}]">
            <template v-for="menu in innerData">
            <div class="panel-header accordion-header" v-Tooltip="getTipOpts(menu)">
                <template v-if="!$scopedSlots.icon">
                <div class="panel-title panel-with-icon"></div>
                <div :class="['panel-icon',menu.iconCls]"></div>
                </template>
                <SideMenuIcon v-if="$scopedSlots.icon" :sidemenu="sidemenu" :menu="menu" :header="true"></SideMenuIcon>
            </div>
            </template>
        </div>
    </div>
    <SideMenuItems v-if="!collapsed" :sidemenu="sidemenu" :data="innerData" :selectedIndex="selectedIndex">
    </SideMenuItems>
</div>
`;

export default {
    name: 'SideMenu',
    template: SIDEMENU_TEMPLATE,
    components: {
        SideMenuItems,
        SideMenuIcon
    },
    props: {
        data: Array,
        selection: Object,
        collapsed: {
            type: Boolean,
            default: false
        },
        border: {
            type: Boolean,
            default: true
        },
        animate: {
            type: Boolean,
            default: true
        },
        multiple: {
            type: Boolean,
            default: true
        },
        floatMenuWidth: {
            type: Number,
            default: 200
        },
        floatMenuPosition: {
            type: String,
            default: 'right'
        }
    },
    data() {
        return {
            // selection: null,
            selectionState: this.selection,
            tipClosed: true,
            innerData: []
        }
    },
    watch: {
        data(value) {
            this.setData(value);
        },
        selection(value) {
            this.selectItem(value);
        },
        selectionState(){
            this.$emit('selectionChange', this.selectionState);
        },
        collapsed(){
            this.selectItem(this.selectionState, true)
        }
    },
    computed: {
        sidemenu(){
            return this;
        },
        selectedIndex(){
            let selected = null;
            let parent = this.selectionState || null;
            while(parent){
                parent = parent.parent;
                if (parent){
                    selected = parent;
                }
            }
            const index = this.innerData.indexOf(selected);
            return index == -1 ? 0 : index;
        }
    },
    created(){
        this.setData(this.data);
    },
    methods: {
        setData(value){
            if (value == null){
                value = [];
            }
            this.innerData = Object.assign([],value);
            treeHelper.$vue = this;
            treeHelper.forNodes(this.innerData, (node) => {
                if (!node.iconCls){
                    this.$set(node, 'iconCls', 'sidemenu-default-icon');
                }
                if (node.children){
                    this.$set(node, 'nodeCls', 'tree-node-nonleaf');
                    if (!node.state){
                        this.$set(node, 'state', 'closed');
                    }
                    if (node.state == 'open'){
                        this.$set(node, 'nodeCls', 'tree-node-nonleaf');
                    } else {
                        this.$set(node, 'nodeCls', 'tree-node-nonleaf tree-node-nonleaf-collapsed');
                    }
                }
            });
        },
        getTipOpts(menu){
            return {
                position: this.floatMenuPosition,
                tooltipCls: 'sidemenu-tooltip',
                valign: 'top',
                propsData: {
                    sidemenu: this,
                    data: menu,
                    tip: true,
                    width: this.floatMenuWidth,
                    selectedIndex: this.selectedIndex
                },
                component: SideMenuItems,
                closed: menu.tipClosed!=null?menu.tipClosed:true,
                tooltipShow: () => {
                    this.$set(menu, 'tipClosed', false);
                },
                tooltipHide: () => {
                    this.$set(menu, 'tipClosed', true);
                }
            };
        },
        selectItem(item, force=false){
            if (item != this.selectionState || force) {
                let parent = null;
                treeHelper.forNodes(this.innerData, (node)=>{
                    (node.children||[]).forEach(n => {
                        if (n==item){
                            parent = node;
                        }
                    });
                });
                if (!this.multiple){
                    this.innerData.forEach(node => {
                        this.$set(node, 'state', 'closed');
                    })
                }
                while(parent){
                    this.$set(parent, 'state', 'open');
                    parent = parent.parent;
                }
                this.selectionState = item;
            }
        },
        onSelectionChange(node){
            this.selectionState = node;
            // this.$emit('selectionChange', this.selectionState);
        },
        onNodeClick(node){
            if (node.children){
                this.$set(node, 'state', node.state=='closed' ? 'open' : 'closed');
                if (node.state == 'open'){
                    this.$set(node, 'nodeCls', 'tree-node-nonleaf');
                } else {
                    this.$set(node, 'nodeCls', 'tree-node-nonleaf tree-node-nonleaf-collapsed');
                }
            } else {
                this.$emit('itemClick', node);
                this.innerData.forEach(menu => this.$set(menu, 'tipClosed', true));
            }
        }
    }
}